.side-panel {
    width: 400px;
    padding: 20px;
    background-color: #f8f9fa;
    position: fixed;
    height: 100%;
    overflow-y: auto;
    border-right: 1px solid #ccc;
  }
  
  .side-panel ul {
    list-style-type: disc; 
    margin-left: 1.5rem; 
    padding: 0; 
  }
  
  .side-panel li {
    margin-bottom: 10px; 
  }
  
  .side-panel a {
    text-decoration: none;
    color: #007bff;
  }
  
  .side-panel a:hover {
    text-decoration: underline;
  }
  
  /* Add spacing between sections and their questions */
  .side-panel ul ul {
    list-style-type: circle; 
    margin-left: 1.5rem; 
    margin-top: 10px; 
  }
  
  /* Add spacing between questions and their answers */
  .side-panel ul ul ul {
    list-style-type: square; 
    margin-left: 1.5rem;
    margin-top: 10px; 
  }
  
  .side-panel h3 {
    margin-bottom: 1rem;
    font-size: 1.5rem;
  }
  
  .correct-answer {
    color: green;
    font-weight: bold;
    margin-left: 5px; /* Add space between the text and the checkmark */
  }